import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
	state: {
		forcedLogin: false, //
		hasLogin: false, //
		token:'',//token
		addrInfo:{//
			lonlat:null,
			name:null,
			adress:null,
			province:null
		},
		userInfo:{
			/* code: "11809086"
			count: 1
			deptName: "/"
			mobile: "139****9420"
			name: ""
			type: 2
			userId: 1478 */
		}
	},  
	mutations: {
		LOGIN(state, userName) { //
			state.userName = userName || '';
			state.hasLogin = true;
		},
		LOGIN_OUT(state) { //
			state.userName = "";
			state.hasLogin = false;
		},
		SET_TOKEN(state, token) {
			state.token = token
		},
		SET_ADDRESS(state,addrInfo){
			state.addrInfo=addrInfo
		},
		SET_USER_INFO(state,userInfo){
			state.userInfo=userInfo
		}
	},
})

export default store
